.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.meta {
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0.5em;
  width: 100%;
  box-sizing: border-box;
  flex-wrap: wrap;
}

.meta > .row {
  display: flex;
  padding: 0.5em 0;
  text-align: left;
  align-items: center;
}

.meta > .row > label {
  padding: 0 1em;
}

.meta > .row > input,
.meta > .row > select {
  width: 7em;
  font-size: 1.2em;
}

.data {
  background-color: #282c34cc;
  width: 100%;
  padding: 1em 0;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.data > .results {

}

.data > .results td {
  padding: 1em 2em;
  font-size: 1.3em;
}

.data > .results tr:nth-child(1) > td,
.data > .results tr > td:nth-child(1) {
  background-color: #00000044;
}

.data > h1 > span {
  color: orangered;
}

.main {
  flex: 1;
  overflow: auto;
}

.main > .results {
  margin-bottom: 3em;
}

.main > .results th {
  background-color: #666;
  color: white;
}

.main > .results th,
.main > .results td {
  padding: 8px;
}

.main > .results th:nth-child(2),
.main > .results th:nth-child(3),
.main > .results th:nth-child(4) {
  width: 15em;
}

.main > .results tr:nth-child(2n) > td {
  background-color: #eee;
}
